form.search-bar{
    position: relative;   
    // transform: translate(-50%,-50%);
    transition: all 1s;
    width: 50px;
    height: 42px;
    // background: rgb(167, 165, 165);
    box-sizing: border-box;
    border-radius: 25px;
    // border: 4px solid rgb(5, 5, 5);
    padding: 5px;
    margin-right: 30px;;

}

form.search-bar input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;;
    height: 42.5px;
    line-height: 30px;
    outline: 0;
    border: 0;
    display: none;
    font-size: 1em;
    border-radius: 0px;
    padding: 0 20px;
    border: 1px solid #ccc;
    border-radius: 10px;;
}

.fa{
    box-sizing: border-box;
    padding: 10px;
    width: 42.5px;
    height: 42.5px;
    position: absolute;
    top: 0px;
    right: 0;
    border-radius: 50%;
    color: #07051a;
    text-align: center;
    font-size: 1.2em;
    transition: all 1s;
    // background-image: url('../img/icons/search.svg');
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;
    background-size: 21px;
    .search-icon {
      fill: #727272;
    }

}

form.search-bar:hover,
form.search-bar:valid{
    width: 200px;
    cursor: pointer;
}

form.search-bar:hover input,
form.search-bar:valid input{
    display: block;
}

form.search-bar:hover .fa,
form.search-bar:valid .fa{
    background-color:  h.$accent-color;
    color: white;
    border-radius: 10px;
    .search-icon {
      fill: #fff;
    }
}


form.search-bar a {
  bottom: 0;
  color: #027cc2;
  display: none;
  font-size: 13px;
  left: -90px;
  position: absolute;
  right: auto;
  text-align: center;
  top: 13px;
  width: 100%;
}

form.search-bar:valid a {
  display: block;
}







