.open-close{
  width: 15px;
  height: 15px;
  position: relative;
  margin-right: 20px;;
  transition: all ease 0.3s;
}

.open-close:after{
  content: "";
  display: block;
  height: 1px;
  width: 100%;
  background-color: #000;
  position: absolute;
  top: 7px;
  transition: all ease 0.3s;
}
.open-close:before{
  content: "";
  display: block;
  height: 1px;
  width: 100%;
  background-color: #000;
  position: absolute;
  transform: rotate(90deg);
  transform-origin: center;
  top: 7px;
}


.mobile-main-menu.open .open-close:before{
  transform: rotate(0deg);
  transform-origin: center;
}

